import React from "react";
import ContactUsSection from "../sections/ContactUsSection";
import ConsoleGroupFeatureSection from "../sections/ConsoleGroupFeatureSection";
import EngineeringSection from "../sections/EngineeringSection";
import jumbo from "../assets/jumbo-engineering.jpg";
import "../framework/site.scss";

export default class EngineeringContent extends React.PureComponent {
  render() {
    return (
      <main>
          <section className="intro">
              <img src={jumbo} alt="Engineering"/>
          </section>
          <EngineeringSection showHeader={false} />
          <ContactUsSection />
          <ConsoleGroupFeatureSection />
      </main>
    );
  }
}