import React from "react";
import "../framework/site.scss";

export interface EngineeringSectionProps {
    showHeader: boolean
}

export default class EngineeringSection extends React.PureComponent<EngineeringSectionProps> {
  render() {
    return (
        <section className="content">
            {this.props.showHeader ? <h5>Engineering</h5> : <></>}
            <p>We have been helping engineer software for decades.</p>
            <p>We help governments, organisations and start-ups build custom software solutions for desktop, web, cloud and mobile.</p>
            <p>Our software engineering experience spans across multiple sectors, including  industrial manufacturing,
                security and surveillance,
                finance and superannuation,
                mission-critical systems for emergency services,
                Queensland Government departments,
                real estate and property management,
                task force management,
                online advertising solutions,
                fitness clubs &amp; gyms,
                and even game development.</p>
            <p>Engineering goes beyond the software development, and includes cloud enablement, UX/UI design, data analytics, software testing and technology advise.</p>
        </section>
    );
  }
}